html,body{margin:0;padding:0;width:100%;height:100%}.info{background-color:#fff;border:2px solid #919191;border-radius:5px}.map_wrap{position:fixed;overflow:hidden;top:0;left:420px;width:calc(100% - 420px);height:100vh}.map_wrap .radius_border{border:1px solid #919191;border-radius:5px}.map_wrap .custom_Overlaycontrol{position:absolute;top:10px;right:140px;overflow:hidden;width:250px;height:32px;margin:0;margin-right:10px;padding:0;z-index:1;font-size:12px;font-family:"Malgun Gothic","맑은 고딕",sans-serif}.map_wrap .custom_Overlaycontrol .custom_checkbox{float:right;display:flex;align-items:center;margin-right:5px}.map_wrap .custom_Overlaycontrol .custom_checkbox input{display:none}.map_wrap .custom_Overlaycontrol .custom_checkbox input:checked+span{color:#fff;background:#425470;background:linear-gradient(#425470, #5b6d8a)}.map_wrap .custom_Overlaycontrol .custom_checkbox span{display:block;width:auto;height:30px;padding:0 10px;text-align:center;line-height:30px;cursor:pointer;background:#fff;background:linear-gradient(#fff, #e6e6e6);border:1px solid #919191;border-radius:5px}.map_wrap .custom_Overlaycontrol .custom_checkbox span:hover{background:#f5f5f5;background:linear-gradient(#f5f5f5, #e3e3e3)}.map_wrap .custom_typecontrol{position:absolute;top:10px;right:10px;overflow:hidden;width:130px;height:30px;margin:0;padding:0;z-index:1;font-size:12px;font-family:"Malgun Gothic","맑은 고딕",sans-serif}.map_wrap .custom_typecontrol span{display:block;width:65px;height:30px;float:left;text-align:center;line-height:30px;cursor:pointer}.map_wrap .custom_typecontrol .btn{background:#fff;background:linear-gradient(#fff, #e6e6e6)}.map_wrap .custom_typecontrol .btn:hover{background:#f5f5f5;background:linear-gradient(#f5f5f5, #e3e3e3)}.map_wrap .custom_typecontrol .btn:active{background:#e6e6e6;background:linear-gradient(#e6e6e6, #fff)}.map_wrap .custom_typecontrol .selected_btn{color:#fff;background:#425470;background:linear-gradient(#425470, #5b6d8a)}.map_wrap .custom_typecontrol .selected_btn:hover{color:#fff}.map_wrap .custom_zoomcontrol{position:absolute;top:50px;right:10px;width:36px;height:80px;overflow:hidden;z-index:1;background-color:#f5f5f5}.map_wrap .custom_zoomcontrol span{display:block;width:36px;height:40px;text-align:center;cursor:pointer}.map_wrap .custom_zoomcontrol span img{width:15px;height:15px;padding:12px 0;border:none}.map_wrap .custom_zoomcontrol span:first-child{border-bottom:1px solid #bfbfbf}.map_wrap .custom_drawcontrol{position:absolute;bottom:10px;left:10px;z-index:1}.map_wrap .custom_drawcontrol button{padding:10px 20px;font-size:14px;border:1px solid #919191;border-radius:5px;background:#fff;background:linear-gradient(#fff, #e6e6e6);cursor:pointer}.map_wrap .custom_drawcontrol button:hover{background:#f5f5f5;background:linear-gradient(#f5f5f5, #e3e3e3)}.map_wrap .custom_drawcontrol button:active{background:#e6e6e6;background:linear-gradient(#e6e6e6, #cccccc)}